        <div v-if="sidebar == 'edit-workflow'" class="fixed inset-0 w-screen min-h-screen z-30 mt-16">
          <div @click="edit = false; sidebar = false" class="absolute cursor-pointer inset-0 bg-gray-500 bg-opacity-75 transition-opacity" aria-hidden="true"></div>
          <div class="right-0 fixed bg-white min-h-screen" style="width: 33vw;">
            <div class="bg-dark text-white h-24 flex flex-row p-4">
              <div class="w-10/12">
                <div class="">Edit {{workflow.name}}</div>
                <div class="text-white text-sm">Edit this workflow by changing important data.</div>
              </div>
              <div class="ml-auto flex flex-row justify-center">
                <svg @click="edit = false" class="text-white w-6 h-6 cursor-pointer" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"></line></svg>
              </div>
            </div>


            <div class="p-4">
              <div class="mt-4">
                <label for="location" class="block font-medium text-gray-700">Name</label>
                <input v-model="workflow.name" type="text" class="text-sm mt-1 block w-full pl-3 pr-10 py-2 border rounded text-base border-gray-300 focus:outline-none focus:border-brand rounded-md" />
              </div>
              <div class="mt-4">
                <label for="location" class="block font-medium text-gray-700">Description</label>
                <textarea v-model="workflow.desc" rows="3" class="text-sm mt-1 block w-full pl-3 pr-10 py-2 border rounded text-base border-gray-300 focus:outline-none focus:border-brand rounded-md">
              </textarea>
              </div>
              <div class="mt-6 flex flex-row">
                <button @click="remove" class="ml-auto text-white mr-2 bg-red-600 py-2 px-3 w-40 text-center cursor-pointer rounded">Remove</button>
                <button @click="save" class="bg-dark py-2 px-3 w-40 text-center cursor-pointer text-white rounded">Save</button>
              </div>
            </div>
          </div>
        </div>




      </div>



    </div>
</template>


<script>
import auth from '@/helpers/auth';
import API from '@/helpers/api'
import env from '@/../env';

import Icon from '@/components/icon'
import Action from '@/components/action'
import CMS from '@/helpers/cms'


export default {
  name: 'Dashboard',
  data() {
    return {
      env,
      user: {},
      pageloaded: false,
      workflow: {},
      edit: false,
      testFlow: false,
      advancedSettings: false,
      showcode: false,

      showSidebar: true,

      active: false, // todo: put in workflow
      sidebar: false,
      saving: false,

      actions: [],
      actionsq: '',
      action: false,

      removeActionWithChildsPopup: false,
      removeActionWithChildsCount: 0,

      addItemIndex: 0,
      addItemBranch: {},

      trigger: false,
      addTriggerPopup: false,
      testFlightPopup: false,
      triggers: CMS.triggers,
    }
  },

  components: {
    Icon,
    Action,
    editor: require('vue2-ace-editor'),
  },

  methods: {

    editorInit: function () {
      require('brace/ext/language_tools')
      require('brace/mode/python')
      //require('brace/theme/xcode'),
      require('brace/theme/chrome')

      require('brace/snippets/python')

      //require('brace/snippets/javascript')
      //require('brace/mode/javascript')

      let editor = this.$refs.myEditor.editor

      // Disable the "find line" with control + l
      editor.commands.removeCommand('find');
      editor.setShowPrintMargin(false);


      editor.container.style.lineHeight = 2
      editor.renderer.updateFontSize()

      editor.setReadOnly(true)
    },

    generateCode: function(){
      var imports = []
      var code = ""
      var data = {};


      // TODO: sorting & if/else cases!
      for (var i = 0; i < this.workflow.actions.length; i++) {
        var actionImports = this.workflow.actions[i].action.code.match(/(import.*?)(?:\r|\n|\r\n)/g);
        var actionCode = this.workflow.actions[i].action.code;
        if(actionImports && actionImports.length > 0){
          for (var im = 0; im < actionImports.length; im++) {
            if(imports.indexOf(actionImports[im].trim()) == -1){
              imports.push(actionImports[im].trim())
            }
            actionCode = actionCode.replace(actionImports[im], '').trim();
          }
        }

        var fields = this.workflow.actions[i].data;
        var fieldNames = Object.keys(fields);
        for (var d = 0; d < fieldNames.length; d++) {
          data[fieldNames[d]] = fields[fieldNames[d]]
        }


        code += `\n\n# ${this.workflow.actions[i].name.trim()}\nautopilot.milestone('${this.workflow.actions[i]._id}') # Log milestone to Autopilot\n${actionCode}`;
      }

console.log('data:', data)

var importcode = "";
for (var c = 0; c < imports.length; c++) {
  importcode += imports[c]+"\n"
}

      this.workflow.code = `# This is the Python code behind your automation.
# Copyright 2021 by WebFaster & Verhaeghe Capital Industries
# This code is generated with Artificial intelligence, if you see any suggestion, let us know.
# Last edited on ${this.moment(this.workflow.updatedAt).format('DD/MM/YYYY HH:mm')}

# 1. Import important packages
import autopilot
${importcode.trim()}

# 2. Define your data
data = ${JSON.stringify(data, undefined, 2)}

# 3. Start the flow
# Register an Autopilot instance
autopilot.register()

${code.trim()}

# Register that we finished
autopilot.finished()`;
      //this.workflow.packages = imports; // TODO: can we map these and give insights / write comments?
    },

    link2elements: function(el1, el2){
      var path = this.findPathBetween2elements(el1, el2);
      //console.log('path:', path)
      var node = document.createElement("div");
      // <!-- stroke-width="3" -->
      node.innerHTML = `<svg style="position: fixed; top: 0; left: 0;" class="line text-gray-300" width="100%" height="100%"><line x1="${path.dom2Coordinate[0]}" y1="${path.dom2Coordinate[1]}" x2="${path.dom1Coordinate[0]}" y2="${path.dom1Coordinate[1]}" stroke="#2c2c2c"/></svg>`
      this.$refs.board.appendChild(node.firstChild);
      //console.log(this.$refs.board.querySelectorAll('.line'))
    },

    extractCoordinates: function(el){
      var width = el.getBoundingClientRect().width;
      var height = el.getBoundingClientRect().height;
      var top = el.getBoundingClientRect().top;
      var left = el.getBoundingClientRect().left;
      return [
          [(left + width/2), top], // top
          [(left + width/2), top + height], // bottom
          //[left, top + (height/2)], // left
          //[left+width, top + (height/2)] // width
        ]
    },

    findPathBetween2elements: function(dom1, dom2) {
      var coordinates1 = this.extractCoordinates(dom1);
      var coordinates2 = this.extractCoordinates(dom2);
      var shortest = 9999;
      var dom1Coordinate = [0, 0];
      var dom2Coordinate = [0, 0]
      for (var i = 0; i < coordinates1.length; i++) {
        for (var j = 0; j < coordinates2.length; j++) {
          var distanceSquared = Math.pow(coordinates1[i][0] - coordinates2[j][0], 2) + Math.pow(coordinates1[i][1] - coordinates2[j][1], 2);
          var distance = Math.sqrt(distanceSquared);
          if(distance < shortest){
            shortest = distance;
            dom1Coordinate = coordinates1[i]
            dom2Coordinate = coordinates2[j]
          }
        }
      }
      return {
        dom1Coordinate,
        dom2Coordinate
      }
    },

    domElement: function(cssSelector){
      return this.$refs.board.querySelector(cssSelector)
    },



    rerender: function(){

      this.removeNodesWithoutParent(); // make sure we clean all the missing nodes.

      this.generateCode();

      if(!this.$refs.board){
        //console.log('this.$refs.board not found')
        return false;
      }

      if(!this.$refs.start){
        //console.log('this.$refs.start not found')
        return false;
      }

      // TODO remove all lines?

      var lines = this.$refs.board.querySelectorAll('.line');
      for (var l = 0; l < lines.length; l++) {
        lines[l].remove();
      }

      var start = this.$refs.start;

      var triggers = this.$refs.board.querySelectorAll('.trigger');
      for (var i = 0; i < triggers.length; i++) {
        this.link2elements(triggers[i], start)
      }

      var actions = this.$refs.board.querySelectorAll('#board .action');
      for (var a = 0; a < actions.length; a++) {
        this.link2elements(actions[a].children[0], start)
      }


      //var actions = this.$refs.board.querySelectorAll('#board .action');
      for (var wfi = 0; wfi < this.workflow.actions.length; wfi++) {
        //this.link2elements(actions[a].children[0], start)
        //console.log(this.workflow.actions[wfi].parent, this.workflow.actions[wfi].id)

        if(this.workflow.actions[wfi].parent && this.workflow.actions[wfi].id){
          var p = this.domElement('#'+this.workflow.actions[wfi].parent);
          var c = this.domElement('#'+this.workflow.actions[wfi].id);
          if(p && c){
            this.link2elements(p, c)
          } else {
            console.log('Link not found 1:', p, c)
          }
        }


        if(this.workflow.actions[wfi].id && this.workflow.actions[wfi].next){
          var n = this.domElement('#'+this.workflow.actions[wfi].next);
          var c2 = this.domElement('#'+this.workflow.actions[wfi].id);
          if(n && c2){
            this.link2elements(c2, n)
          } else {
            console.log('Link not found 2:', n, c2)
          }
        }




        if(this.workflow.actions[wfi]){
          var top = this.domElement('#'+this.workflow.actions[wfi].id+ ' .top');
          var bottom = this.domElement('#'+this.workflow.actions[wfi].id+' .bottom');
          if(top && bottom){
            this.link2elements(top, bottom)
          }
        }


      }

    },


    findActions: async function(){
      var actions = await API.get('/actions?q='+this.actionsq);
      this.actions = actions.data;
      //console.log('this.actions:', actions)
    },

    reset: async function(){
      this.action = false;
      this.showcode = false;
      this.sidebar = false;
      this.testFlightPopup = false;
      this.removeActionWithChildsPopup = false;
      this.rerender()
    },

    addTrigger: function(id){
      if(!this.triggers[id].active){
        alert("We're still working on this feature.")
        return false
      }

      this.workflow.triggers.push({'triggerType': id });
      this.addTriggerPopup = false;

      const _self = this;
      setTimeout(function(){ _self.rerender() }, 50)
    },

    addItem: async function(branch){
      this.actionsq = '';
      this.addItemBranch = branch;
      this.sidebar = 'add-action';
    },

    editAction: async function(action){
      console.log('action:', action)
      this.action = action;
      this.sidebar = 'edit-action'
    },


    findchildren: function(parentId){
      var data = [];
      for (var i = 0; i < this.workflow.actions.length; i++) {
        if(this.workflow.actions[i] && this.workflow.actions[i].parent == parentId){
          data.push(this.workflow.actions[i]);
        }
      }
      return data;
    },


    findParent: function(id){
      for (var i = 0; i < this.workflow.actions.length; i++) {
        if(this.workflow.actions[i].id == id){
          return this.workflow.actions[i]
        }
      }
      return false;
    },

    removeNodesWithoutParent: function(){
      console.log('____ removeNodesWithoutParent ____')
      for (var i = 0; i < this.workflow.actions.length; i++) {
        if(this.workflow.actions[i].parent !== ''){
          var parent = this.findParent(this.workflow.actions[i].parent)
          if(!parent){
            // don't just use i to remove, as we're changing this entire array multiple times during this function.
            this.workflow.actions.splice(this.workflow.actions.indexOf(this.workflow.actions[i]), 1)
          }
        }
      }
    },

    addAction: async function(action){

      var item = {
        name: action.name,
        id: 'a_'+new Date().getTime(),
        action: action,
        data: {},
        parent: this.addItemBranch.id || '',
      };

      //var parent = findParent()
      console.log('Parent:', this.addItemBranch)
      console.log('ITEM:', item)

      /*

        We can't use the ._id key to check if this node is the upper node, as one refresh every single action gets a _id
        Therefore we can check if you are adding something to the parent node by testing if there is a .team, because only the workflow will have this key.

      */
      if(this.addItemBranch.team){
        console.log('[LOG] 1. Added an item at the first level.')
        var rootChilds = this.findchildren('');
        console.log('[LOG] 2. Root childs:', rootChilds)
        if(rootChilds.length < 1){
          // 0 chilren, so just add to bottom
          console.log('[LOG] 3. No root childs, so just push to an empty array.')
          this.workflow.actions.push(item);
        } else {
          console.log('[LOG] 4. Root childs detected, so push all of them 1 row in the chain.')
          for (var rc = 0; rc < rootChilds.length; rc++) {
            rootChilds[rc].parent = item.id;
          }
          this.workflow.actions.push(item);
        }
      } else {
        console.log('[LOG] 5. adding an item further in the chain (not first element)')
        if(this.addItemBranch.action.id == 'ifelse'){
          console.log('[LOG] 6. Added an item to an if/else statement.');
          this.workflow.actions.push(item);
        } else {
          console.log('[LOG] 7. Adding an element to the chain (no if-else)')
          var children = this.findchildren(this.addItemBranch.id);
          console.log('[LOG] 8. Current children for this parent node are:', children)
          if(children.length < 1){
            console.log('[LOG] 9. Parent node has no children yet, so just push this.')
            // 0 chilren, so just add to bottom
            this.workflow.actions.push(item);
          } else {
            console.log('[LOG] 10. Parent node has multiple children, so pushing further in the chain.')
            for (var c = 0; c < children.length; c++) {
              children[c].parent = item.id;
            }
            this.workflow.actions.push(item);
          }
        }
      }

      // find parent & see if this is a ifelse



      console.log(this.workflow.actions.length)

      this.save();

      const _self = this;
      setTimeout(function(){ _self.rerender() }, 50)
    },

    saveAction: async function(){
      this.action = false;
      this.sidebar = ""
      await this.save();
    },


    removeActionWithChilds: async function(answer){

      if(answer == 'branch'){
        var data = [];
        for (var w = 0; w < this.workflow.actions.length; w++) {
          if(this.workflow.actions[w] && this.workflow.actions[w].id !== this.action.id && this.workflow.actions[w].parent !== this.action.id){
            data.push(this.workflow.actions[w])
          }
        }
        this.workflow.actions = data;
      } else {
        var parentId = this.action.parent
        this.workflow.actions.splice(this.workflow.actions.indexOf(this.action), 1);
        for (var x = 0; x < this.workflow.actions.length; x++) {
          if(this.workflow.actions[x].parent == this.action.id){
            this.workflow.actions[x].parent = parentId
          }
        }
      }

      this.removeActionWithChildsPopup = false;
      this.sidebar = false;
      this.action = false;
      this.save();
    },

    removeAction: async function(){
        var childs = 0
        for (var i = 0; i < this.workflow.actions.length; i++) {
          if(this.workflow.actions[i] && this.workflow.actions[i].parent == this.action.id){
            childs += 1;
          }
        }

        if(childs < 1){
          this.workflow.actions.splice(this.workflow.actions.indexOf(this.action), 1);
          this.sidebar = ""
          this.action = false;
          this.save();
        } else {
          this.removeActionWithChildsCount = childs;
          this.removeActionWithChildsPopup = true
          return false;
        }
    },
    removeTrigger: function(){
      this.workflow.triggers.splice(this.workflow.triggers.indexOf(this.trigger), 1);
      this.trigger = false;
      this.sidebar = false;
    },
    switchActive: function(){
      this.workflow.active = !this.workflow.active;
      this.save();
    },
    saveTrigger: function(){
      this.trigger = false;
      this.sidebar = false;
      this.save(false);
    },
    editTrigger: function(trigger){
      this.sidebar = 'edit-trigger';
      this.trigger = trigger
    },
    autosave: async function(){
      this.save(false)
    },
    save: async function(close=true){
      if(!this.workflow.name || this.workflow.name.length < 3){
        alert('Your workflow name should have at least 3 characters');
        return false;
      }

      // TODO: save actions

      this.saving = true;
      const _self = this;
      setTimeout(function(){
        _self.saving = false;
      }, 1000)

      await API.put('/workflows/'+this.workflow._id, this.workflow);
      if(close){
        this.sidebar = false;
      }
    },
    remove: async function(){
      if(confirm('Are you sure you want to remove this workflow?')){
        await API.delete('/workflows/'+this.workflow._id);
        this.$router.push({path: '/workflows'})
      }
    },
  },

  watch: {
    actionsq: async function(){
      await this.findActions()
    },
    sidebar: async function(){
      var _self = this;
      setTimeout(function(){
        _self.rerender()
      }, 20);
    }
  },

  async mounted() {

    // Test to login a user
    this.user = await auth.me().catch(function(e){
      console.log('NOT LOGGEDIN', e)
    });

    var workflow = await API.get('/workflows/'+this.$route.params.id);
    this.workflow = workflow;
    //console.log('********** PAGE LOADED')
    //console.log('workflow:', workflow)

    await this.findActions();

    this.pageloaded = true;


    const _self = this;

    setTimeout(function(){
      _self.rerender();
    }, 100)


    window.addEventListener('keydown', async function(event) {

      console.log('TODO: check if your on the right route. ROUTE:', _self.$route)

      if (event.key == "Escape") {
        console.log('____')
        _self.reset()
      }

      //console.log('event.target.nodeName:', event.target.nodeName)
      if(['INPUT', 'TEXTAREA'].indexOf(event.target.nodeName) > -1){
        return false; // block from executing the following lines of code.
      }


      /*
      if ((event.ctrlKey || event.metaKey) && event.key == 'a') {
        event.preventDefault();
        _self.leftpanel = 'addItem'
        return false;
      }
      */


    });



    window.addEventListener('resize', async function(){
      _self.rerender()
    });

    document.addEventListener('scroll', function() {
        _self.rerender()
    });

  }
}
</script>

