<template>
    <div v-if="pageloaded" class="flex flex-row mt-20">
      <div class="w-9/12 mx-auto">
        {{event}}

        <div>Share</div>
        <div class="cursor-pointer" @click="remove">Delete</div>
      </div>
    </div>
</template>


<script>
import auth from '@/helpers/auth';
import API from '@/helpers/api'

export default {
  name: 'Dashboard',
  data() {
    return {
      pageloaded: false,
      user: {},
      event: {}
    }
  },
  methods: {
    remove: async function(){
      if(confirm('Are you sure you want to unpin this plugin?')){
        await API.delete('/events/'+this.event._id);
        this.$router.push({path: '/events'})
      }
    },
  },
  async mounted() {
    //const _self = this;

    // Test to login a user
    this.user = await auth.me().catch(function(e){
      console.log('NOT LOGGEDIN', e)
    });

    var event = await API.get('/events/'+this.$route.params.id);
    this.event = event;


    this.pageloaded = true;


  }
}
</script>

