import Vue from 'vue'
import Router from 'vue-router'


import Login from '@/pages/auth/login.vue'
import TokenValidation from '@/pages/auth/tokenValidation.vue'
import Token from '@/pages/auth/token.vue'


import Teams from '@/pages/teams/overview.vue'
import TeamSettings from '@/pages/teams/settings.vue'
import TeamNew from '@/pages/teams/new.vue'


import Workflows from '@/pages/workflows/index.vue'
import WorkflowsDetail from '@/pages/workflows/detail.vue'


import Plugins from '@/pages/plugins/index.vue'
import PluginsNew from '@/pages/plugins/new.vue'
import PluginsDetail from '@/pages/plugins/detail.vue'


import Collections from '@/pages/collections/index.vue'
import Events from '@/pages/events/index.vue'
import Event from '@/pages/events/detail.vue'


//import Homepage from '@/pages/homepage.vue'


import Account from '@/pages/account/settings.vue'


Vue.use(Router);

window.routes = [


  //{ path: '/', name: 'Homepage', component: Homepage },
  { path: '/', redirect: '/workflows' },

  { path: '/login', name: 'login', component: Login },
  { path: '/token', name: 'token', component: Token },
  { path: '/token/:token', name: 'magic-link', component: TokenValidation },

  { path: '/teams', name: 'teams', component: Teams },
  { path: '/teams/new', name: 'new-team', component: TeamNew },
  { path: '/settings', name: 'team', component: TeamSettings },


  { path: '/workflows', name: 'events', component: Workflows },
  { path: '/workflows/:id', name: 'event-detail', component: WorkflowsDetail },


  { path: '/plugins', name: 'events', component: Plugins },
  { path: '/plugins/new', name: 'event-new', component: PluginsNew },
  { path: '/plugins/:id', name: 'event-detail', component: PluginsDetail },


  { path: '/collections', name: 'events', component: Collections },
  { path: '/events', name: 'events', component: Events },
  { path: '/events/:id', name: 'event-detail', component: Event },


  { path: '/account', name: 'account', component: Account },


]

const router = new Router({
  mode: 'history',
  scrollBehavior() {
   return { x: 0, y: 0 }; // open another page => go to top!
  },
  routes: window.routes
})

export default router
