<script>
import auth from '@/helpers/auth';
import API from '@/helpers/api'

export default {
  name: 'Dashboard',
  data() {
    return {
      page: 'home',
      subpage: 'general',
      user: {},
      addAction: false,

      pageloaded: false,
      plugin: {
        actions: [],
      },
      action: false,

      // sidebar
      settings: false,
    }
  },

  components: {
      editor: require('vue2-ace-editor'),
  },


  methods: {

    save: async function(){
      if(!this.plugin.name || this.plugin.name.length < 3){
        alert('Your plugin name should have at least 3 characters');
        return false;
      }

      await API.put('/plugins/'+this.plugin._id, this.plugin);
      this.settings = false;

    },
    remove: async function(){
      if(confirm('Are you sure you want to unpin this plugin?')){
        await API.delete('/plugins/'+this.plugin._id);
        this.$router.push({path: '/plugins'})
      }
    },

    /* Actions */
    createAction: async function(){
      this.addAction.code = ""
      this.addAction.fields = []
      this.addAction.plugin = this.plugin._id;
      var action = await API.post('/actions', this.addAction);
      this.plugin.actions.push(action);
      this.addAction = false;
    },
    saveActionWithClose: async function(){
      this.saveAction();
      if(close){
        this.page = 'home';
      }
    },
    saveAction: async function(){
      if(!this.action.name || this.action.name.length < 3){
        alert('Your action name should have at least 3 characters');
        return false;
      }

      /*
      if(this.action.code.indexOf('next()') == -1){
        this.action.isEnd = true;
      } else {
        this.action.isEnd = false;
      }
      */


      await API.put('/actions/'+this.action._id, this.action);


    },

    saveActionCode: async function(){
      this.saveAction()
      alert('Saved.')
    },
    removeAction: async function(){
      if(confirm('Are you sure you want to unpin this action? You can always add it again later on.')){
        await API.delete('/actions/'+this.action._id);
        this.plugin.actions.splice(this.plugin.actions.indexOf(this.action), 1)
        this.action = false;
        this.page = 'homepage'
      }
    },


    editorInit: function () {
      require('brace/ext/language_tools')
      require('brace/mode/python')
      //require('brace/theme/xcode'),
      require('brace/theme/chrome')

      require('brace/snippets/python')

      //require('brace/snippets/javascript')
      //require('brace/mode/javascript')

      let editor = this.$refs.myEditor.editor

      // Disable the "find line" with control + l
      editor.commands.removeCommand('find');
      editor.setShowPrintMargin(false);


      editor.container.style.lineHeight = 2
      editor.renderer.updateFontSize()
    }

  },
  async mounted() {
    //const _self = this;

    // Test to login a user
    this.user = await auth.me().catch(function(e){
      console.log('NOT LOGGEDIN', e)
    });

    var plugin = await API.get('/plugins/'+this.$route.params.id);
    this.plugin = plugin;


    this.pageloaded = true;


  }
}
</script>

