<template>
    <div class="w-full z-0 bg-white mt-14">

        <div class="py-10 w-9/12 mx-auto">

          <div class="flex flex-col mt-10">
              <div class="flex flex-row">
                  <div class="px-2">
                      <div class="text-4xl font-bold">Hi {{user.firstname}} </div>
                      <div class="text-gray-600 text-lg mt-1">Here are all the teams that you have access to. For which team do you want to update?</div>
                  </div>
              </div>




            <div class="flex flex-row flex-wrap mt-10">



              <router-link to="/teams/new" class="w-64 h-48 lg:w-3/12 lg:h-64 p-2">
                <div class="w-full h-full bg-white text-2xl cursor-pointer rounded border hover:border-branding flex flex-row items-center justify-center text-center">
                  Add Team 
                </div>
              </router-link>


              <div @click="select(team)" v-for="team in user.teams" v-bind:key="team._id" class="w-64 h-48 lg:w-3/12 lg:h-64 p-2">
                <div :class="team._id == user.team._id ? 'border-brand' : ''" class="w-full h-full bg-white text-2xl cursor-pointer rounded border hover:border-branding flex flex-row items-center justify-center text-center">
                  {{team.name}}
                </div>
              </div>


            </div>






          </div>






        </div>
    </div>
</template>


<script>
import auth from '@/helpers/auth';
import API from '@/helpers/api'


export default {
  name: 'Dashboard',
  data() {
    return {
      user: {},
      greeting: '',
    }
  },
  methods: {
    select: async function(team){

      if(team._id == this.user.team._id){
        alert('You have already selected this team.');
        return false;
      }

      await API.put('/users/me', { team: team._id });
      this.$parent.team = team;
      this.$router.push({path: '/'});
      //document.location.reload()
    },
  },
  async mounted() {

    // Test to login a user
    this.user = await auth.me().catch(function(e){
      console.log('NOT LOGGEDIN', e)
    });

  }
}
</script>

