                      <div class="text-gray-600 mt-1">Create a new plugin by filling in the following details.</div>
                  </div>
              </div>

            <div class="flex flex-col flex-wrap mt-10 w-full">

              <div class="w-full">
                <div class="block font-medium text-gray-700">Name</div>
                <div class="text-xs text-gray-600 py-1">The name of your app as it should display in Autopilot.</div>
                <input v-model="plugin.name" type="text" class="text-sm mt-1 block w-full pl-3 pr-10 py-2 border rounded text-base border-gray-300 focus:outline-none focus:border-brand rounded-md" />
              </div>

              <div class="w-full mt-4">
                <div class="block font-medium text-gray-700">Short description</div>
                <div class="text-xs text-gray-600 py-1">A sentence describing your app in 140 characters or less, e.g. "Trello is a team collaboration tool to organize tasks and keep projects on track."</div>
                <textarea rows="4" v-model="plugin.desc" class="text-sm mt-1 block w-full pl-3 pr-10 py-2 border rounded text-base border-gray-300 focus:outline-none focus:border-brand rounded-md"></textarea>
              </div>

              <div class="w-full mt-4">
                <div class="block font-medium text-gray-700">Category</div>
                <div class="text-xs text-gray-600 py-1">How would you categorize your app? Choose the most appropriate option for your app's core features.</div>
                <select v-model="plugin.category" class="cursor-pointer border mt-1 block w-full pl-3 pr-2 py-2 focus:outline-none rounded-md">
                  <option value="" disabled>Choose value</option>
                  <option value="marketing">Marketing</option>
                  <option value="technology">Technology</option>
                  <option value="sales">Sales</option>
                  <option value="other">Other</option>
                </select>
              </div>


            </div>




            <div class="mt-6 flex flex-row">
              <button @click="create" class="ml-auto text-white bg-brand py-3 px-3 w-48 text-center cursor-pointer rounded">Add plugin</button>
            </div>

          </div>

        </div>
    </div>
</template>


<script>
import auth from '@/helpers/auth';
import API from '@/helpers/api'


export default {
  name: 'Dashboard',
  data() {
    return {
      user: {},
      plugin: {
        category: 'marketing'
      },
      step: 1,
    }
  },
  methods: {
    create: async function(){
      if(!this.plugin.name || this.plugin.name.length < 3){
        alert('Your event name should have at least 3 characters');
        return false;
      }

      await API.post('/plugins', this.plugin);
      this.$router.push({path: '/plugins'})
    }
  },


  computed: {
  },

  async mounted() {

    // Test to login a user
    this.user = await auth.me().catch(function(e){
      console.log('NOT LOGGEDIN', e)
    });



  }
}
</script>

